import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Button, FormControl, FormLabel, Input, Select
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { apiErrorHandler } from '../../../utils/general';
import { achPay } from '../../../store/middlewares/payment';
import CurrencyInput from '../../../components/CurrencyInput';
import { useDrawer } from '../../../hooks/drawer';

const MoveFundsAnotherBankSuccess = React.lazy(() => import('../MoveFundsAnotherBankSuccess/MoveFundsAnotherBankSuccess'));

const MoveFundsAnotherBankInfo = () => {
  const { t } = useTranslation();
  const { data, open } = useDrawer<{contactId: string}>();
  const { selectedAccount: account, selfBankAccounts } = useAppSelector((store) => store.account, shallowEqual);

  const dispatch = useAppAsyncDispatch();

  const [selectedContact, selectContact] = useState(data?.contactId);
  const [amount, updateAmount] = useState('');
  const [description, updateDesc] = useState('');

  const isValid = useMemo(() => amount !== '' && description !== '' && selectedContact !== '', [selectedContact, amount, description]);

  const onSubmit = useCallback(() => {
    if (isValid && account && selectedContact) {
      dispatch(achPay({
        accountId: account.id,
        amount,
        contactId: selectedContact,
        description
      }))
        .then(() => {
          open(MoveFundsAnotherBankSuccess);
        })
        .catch(apiErrorHandler);
    }
  }, [selectedContact, account, isValid, amount, description]);

  return (
    <div className="page">
      <MiniBox
        heading={t('move_funds_info.title')}
        footer={(
          <Button width="100%" variant="primary" onClick={onSubmit} disabled={!isValid}>
            {t('move_funds_info.ctaText')}
          </Button>
)}
      >
        <Box width="100%" paddingTop="15px">
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('move_funds_info.source')}
            </FormLabel>
            <Input
              disabled
              value={account?.label}
              variant="primary"
            />
          </FormControl>
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('move_funds_info.destination')}
            </FormLabel>

            <Select
              variant="primary"
              value={selectedContact}
              onChange={(e: React.ChangeEvent<HTMLSelectElement>) => selectContact(e.target.value)}
            >
              {selfBankAccounts.map((b) => (
                <option key={b.id} value={b.id}>
                  {b.ach.bankName}
                  {' '}
                  XXXXXX
                  {' '}
                  {b.ach.accountNumber.slice(b.ach.accountNumber.length - 4)}
                </option>
              ))}

            </Select>
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('move_funds_info.amount')}
            </FormLabel>
            <CurrencyInput
              value={amount}
              onChange={(v) => {
                updateAmount(v);
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('move_funds_info.description')}
            </FormLabel>
            <Input
              required
              value={description}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => updateDesc(e.target.value)}
              variant="primary"
            />
          </FormControl>
        </Box>
      </MiniBox>

    </div>
  );
};

export default MoveFundsAnotherBankInfo;
